body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: white;
  height: 100vh;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.Projects {
    padding:50px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size:14px;
    height: 80vh;
}

.Projects--loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
a, a::after {
    color: black;
    text-decoration: none;
}

.Project{
    background-color: white;
    width: 45%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 50px;
    text-overflow: ellipsis;
    padding: 0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Project__Column{
    width: 40%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}

.Project__Column:last-child{
    padding: 20px 0;
    width: 50%;
}

.Project__Header{
    display: flex;
}

.Project__Thumbnail{
    max-width: 100%;
    position: relative;
    top: -25px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

.Project__Title__ul{
    list-style: none;
    padding-left: 20px;
}

.Project__Title__li{
    display: inline-block;
}

.Project__Title__img{
    width: 30px;
    margin-right: 10px;
}

.Project__Skills__ul{
    list-style: none;
    display: flex;
    padding-left: 0;
    text-overflow: ellipsis;

}

.Project__Skills__li{
    margin-right: 5px;
}

@media screen and (min-width:320px) and (max-width:667px){
    .Project{
        width: 100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Project{
        width: 100%;
        flex-direction: column;
    }
    .Project__Thumbnail{
        top: 0;
        left: 0;
        width: 100%;
    }
    .Project__Column{
        width: 100%!important;
    }
}
